* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat','IBM Plex Sans', 'Open Sans',sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  /* height: "100vh"; */
  /* background-color: red; */
}

#sidebar{
  /* background: red; */
  padding-block-start: 2.8rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: space-between;
}

#sidebar nav {
}


#sidebar nav ul {
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
}

#sidebar nav ul li {
  height: 60px;
  width: 100%;
}

#sidebar nav a {
  height: 100%;
  /* 
    flex-direction
    white-space: pre;
    padding: 0.5rem;
    border-radius: 8px;
    color: inherit;
    text-decoration: none;
    gap: 1rem;
  */

  display: flex;
  /* flex-direction: column; */
  align-items: center;
  align-content: center;
  /* justify-content: center; */

  /* display: block; */
  text-decoration: none;
}


#sidebar nav a:hover {
  /* background: #e3e3e3; */

}

#sidebar nav a.active {
  /* background: hsl(224, 98%, 58%); */
  background-color: rgba(255, 255, 255, 0.15);
  width: 100%;
  color: white;

}

#sidebar nav a.pending {
  /* color: hsl(224, 98%, 58%); */
  color: rgba(255, 255, 255, 0.15);
}

#sidebar nav ul {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content:center;
  align-items: center;
  width: 100%;
  list-style-type: none;
  padding-inline: 4px;
}
